import React from "react"
import { Button, PopoutWrapper, Alert } from "@vkontakte/vkui"
import { useDispatch } from "react-redux"
import { closePopout } from "../store/router/actions"

const AlertOffline = () => {
  const dispatch = useDispatch()
  return (
    <Alert
      actions={[
        {
          title: "Закрыть",
          autoclose: true,
          mode: "cancel",
        },
      ]}
      header='Offline'
      onClose={() => dispatch(closePopout())}
    >
      Пропало интернет соединение! Можно продолжать играть, но прогресс не
      сохранится, а получить подсказки не получится
    </Alert>
  )
}
export default AlertOffline
